@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@300;400;500;600;700);
*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
  text-decoration: none;
  list-style: none;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  scroll-behavior: smooth;
  position: relative;
}

:root {
  --background: #eee;
  --color-grey: grey;
  --color-white: #fff;
  --color-black: #000;
  --color-write: #333;
  --color-light-grey: #ccc;
  --background-color: #fafafa;
  --shadow-color: rgba(0, 0, 0, 0.8);
  --secondary: #4db3ce;
  --primary: #203238;
  --transparent: transparent;
}
body {
  font-family: "Mulish", sans-serif;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 1.7;
}

.contact-banner{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:6% 25%;text-align:center;font-size:1.6rem;line-height:30px;background-color:var(--secondary);color:var(--color-white)}@media only screen and (max-width: 650px){.contact-banner{padding:6% 10%}}.contact-banner h3{font-size:28px;font-weight:300;margin-bottom:30px}.contact-banner p{font-weight:300;margin-bottom:40px}.contact-banner button{outline:none;padding:12px 30px;background-color:transparent;border:2px solid var(--color-white)}.contact-banner button:hover{background-color:var(--color-white)}.contact-banner button:hover .contact-banner__link{color:var(--secondary);text-decoration:none}.contact-banner__link{color:var(--color-white);font-weight:700}

.footer{padding:120px 120px 0px 140px;color:var(--color-white);background-color:var(--primary);display:flex;flex-direction:column;justify-content:center;align-items:center;font-size:1.6rem;line-height:30px;color:var(--color-grey)}@media only screen and (max-width: 650px){.footer{padding:80px}}.footer__details{margin-bottom:50px;padding:0 30px;text-align:left}@media only screen and (max-width: 650px){.footer__details{padding:0px}}.footer__details--social{margin-top:40px;display:flex}.footer__details--social a{display:flex;justify-content:center;align-items:center;border-radius:50%;font-size:18px;text-decoration:none}.footer__details--social-1{height:40px;width:40px;color:var(--color-white);border:1px solid var(--color-white)}.footer__details--social-1:hover{background:var(--color-white);color:var(--secondary)}.footer__details--social-1:not(:first-child){margin-left:5px}.footer__details p{display:flex;flex-direction:column}.footer__details p .main-details{color:var(--color-white)}.footer__details h3{color:var(--color-white);font-size:20px;text-transform:uppercase;font-weight:800;margin-bottom:20px;letter-spacing:1px}.footer__details--links{text-decoration:none;color:var(--color-light-grey);border-bottom:0.5px solid var(--color-light-grey)}.footer__details--links:hover{text-decoration:none;color:var(--background);border:none}.footer__details ul li{margin-top:10px}.footer__end{margin:80px 0}.footer__end a{margin-left:5px;text-decoration:none;color:var(--color-light-grey);border-bottom:0.5px solid var(--color-light-grey)}.footer__end a:hover{text-decoration:none;color:var(--background);border:none}

.navbar{padding:0.5rem 12%;background-color:transparent}.navbar img{width:4rem}.navbar-nav .nav-item:not(:last-child){margin-right:3rem}.navbar-nav .nav-link{font-size:1.4rem;letter-spacing:1.5px;font-weight:500;color:var(--color-white);line-height:3rem;position:relative}.navbar-nav .nav-link:hover{color:var(--secondary);transition:0.5s}.navbar-nav .nav-link:hover::before{width:80%;transform:scale(1)}.navbar-nav .nav-link::before{content:"";bottom:0;left:10%;border-bottom:3px solid var(--secondary);position:absolute;transition:transform 0.5s ease-in-out;transform:scale(0)}@media only screen and (max-width: 770px){.navbar-nav .nav-link::before{border:none}}.navbar-toggler i{color:var(--color-white);font-size:2.2rem}.navbar-toggler i:active{color:var(--secondary)}.affix{transition:all 0.8s;background-color:var(--primary)}.affix img{width:3.5rem}.affix .nav-link{font-size:1.2rem;letter-spacing:2px;font-weight:600}



.about-section{padding:100px 8%}.about-section__details{padding:30px 80px 30px 30px;display:flex;flex-direction:column;justify-content:center}.about-section__details h2{font-size:18px;font-weight:300;text-transform:uppercase;color:var(--color-grey);margin-bottom:60px;position:relative}.about-section__details h2::before{content:"";bottom:-30px;left:-20px;width:20%;border-bottom:8px solid var(--secondary);position:absolute}.about-section__details h4{font-size:35px;text-transform:uppercase;font-weight:300;margin-bottom:30px}.about-section__details h4 span{font-weight:900}.about-section__details p{font-size:18px;color:var(--color-grey);font-weight:300;margin-bottom:60px}.about-section__details--link{width:180px;outline:none;padding:14px 30px;background-color:transparent;color:var(--secondary);font-size:16px;font-weight:900;text-transform:uppercase;border:2px solid var(--secondary)}.about-section__details--link span{font-size:20px;margin-left:10px}.about-section__details--link:hover{background-color:var(--secondary);color:var(--color-white);text-decoration:none}.about-section__image{padding:30px;position:relative}.about-section__image img{z-index:20;width:100%}.about-section__image::before{content:"";position:absolute;width:100%;top:0;left:0;right:0;bottom:0;background:rgba(20,15,15,0.5)}.about-section__image::before{content:"";z-index:-10;position:absolute;width:90%;height:90%;top:50px;right:0;left:45px;bottom:0;background:var(--background)}@media only screen and (max-width: 1250px){.about-section__image::before{display:none}}

.project-completed{padding:100px 8%}

.headbanner{height:100vh;width:100%;background-size:cover;background-position:center;background-color:black;position:relative;display:flex;justify-content:center;align-items:center;text-align:center}.headbanner::before{content:"";position:absolute;width:100%;top:0;left:0;right:0;bottom:0;background:rgba(20,15,15,0.5)}.headbanner__details{padding:0 20px;z-index:1000;width:800px;color:var(--color-white)}.headbanner__details h1{color:var(--secondary);text-transform:capitalize;font-size:60px;font-weight:900;margin-bottom:30px}@media only screen and (max-width: 650px){.headbanner__details h1{font-size:30px}}.headbanner__details p{font-size:18px;font-weight:500;margin-bottom:40px}@media only screen and (max-width: 650px){.headbanner__details p{font-size:14px}}.headbanner__details--link{outline:none;padding:14px 30px;background-color:transparent;color:var(--color-white);font-size:16px;font-weight:900;text-transform:uppercase;border:2px solid var(--color-white)}.headbanner__details--link span{font-size:20px;margin-left:10px}.headbanner__details--link:hover{background-color:var(--secondary);border:2px solid var(--secondary);color:var(--white);text-decoration:none}



.headingbanner{height:70vh;width:100%;background-size:cover;background-position:center;background-color:black;color:var(--color-white);display:flex;flex-direction:column;justify-content:center;align-items:center;position:relative}.headingbanner::before{content:"";position:absolute;width:100%;top:0;left:0;right:0;bottom:0;background:rgba(0,0,0,0.5)}.headingbanner h1{z-index:1000;text-transform:uppercase;font-size:75px;font-weight:700}@media only screen and (max-width: 650px){.headingbanner h1{font-size:40px}}.headingbanner p{z-index:1000;margin-top:20px;font-size:18px;display:flex;justify-content:space-between;width:170px}.headingbanner p .bottom{text-decoration:underline}@media only screen and (max-width: 650px){.headingbanner p{width:100px;font-size:12px}}


